<template>
  <div>
    <div class="wrapper" ref="wrapper">
      <div class="content" ref="listView">
        <div class="top-tip">
          <span class="refresh-hook">{{tips}}</span>
        </div>
        <div v-if="isShowAlert" class="alert">刷新成功</div>
        <div v-if="recordList.length">
          <div class="list" @click="inPageDetail(key, item)"  v-for="(item, key) in recordList">
            <p><span class="f2">时间：{{item.tradeTime}}</span> <span>操作：{{item.typeLabel}}</span>   </p>
            <p> <span class="f2">期数：{{item.period == null ? '-' : item.period}}</span> <span>下注：{{item.money == 0 ? '-' : item.money}}</span></p>
            <p> <span class="f2">余额：{{item.balance}}</span> </p>
          </div>
        </div>
        <div v-if="!recordList.length"> <h3 style="padding-top: 15px;font-weight: bold;">暂无数据</h3></div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import {getRecord} from 'api/bet'
  import Bus from 'common/js/bus'

  export default {
    data () {
      return {
        acc: 'lei',
        recordList: [],
        count: 1,
        tips: '下拉刷新',
        isShowAlert: false
      }
    },
    created () {
      this._loadData()
    },
    mounted () {
      Bus.$on('betSuccess', function () {
        this._loadData()
      })
    },
    methods: {
      _loadData () {
        getRecord().then((res) => {
          this.recordList = res.data.data
          this.$nextTick(() => {
            if (!this.scroll) {
              this.scroll = new BScroll(this.$refs.wrapper, {probeType: 1, click: true})
              this.scroll.on('touchEnd', (pos) => {
                if (pos.y > 50) {
                  this.tips = '释放立即刷新'
                  var timer = setTimeout(() => {
                    this._loadData()
                    this.tips = '下拉刷新'
                    this._refreshAlert()
                  }, 500)
                }
              })
            } else {
              this.scroll.refresh()
            }
          })
        })
      },
      _refreshAlert () {
        this.isShowAlert = true
        setTimeout(() => {
          this.isShowAlert = false
        }, 1000)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .wrapper
    position: fixed;
    z-index: 1;
    top: 60px;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #fff;
    overflow: hidden;
    .content
      background: #fff
      min-height 95%
      .alert
        width: 100%
        height: 40px
        line-height: 40px
        position: absolute
        left: 0
        top: 0
        background: rgba(25, 0, 0, 0.5)
        color: #fff
      .top-tip
        position: absolute;
        top: -40px;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: #555
      .list
        width: 100%
        box-sizing border-box
        padding 10px
        border: 1px solid #ddd
        height: 75px
        font-size: 12px
        text-align left
        p
          line-height: 18px
          display: flex
          span
            flex 1
            margin-left: 10px
            &.f2
              flex 2
</style>


