export function bet(data) {
  var url = '/client/bet'
  return axios({
    url: url,
    method: 'post',
    data: data,
    withCredentials: true
  }).then((res) => {
    return Promise.resolve(res)
  })
}

export function getBalance(mobile) {
  var url = '/client/user/mobile/' + mobile
  return axios.get(url).then((res) => {
    return Promise.resolve(res)
  })
}


export function getLottery() {
  var url = '/client/lottery/current-period'
  return axios.get(url).then((res) => {
    return Promise.resolve(res)
  })
}


export function getHistory() {
  function getNowFormatDate() {
    var date = new Date();
    var seperator1 = "-";
    var year = date.getFullYear();
    var month = date.getMonth() + 1;
    var strDate = date.getDate();
    if (month >= 1 && month <= 9) {
      month = "0" + month;
    }
    if (strDate >= 0 && strDate <= 9) {
      strDate = "0" + strDate;
    }
    var currentdate = year + seperator1 + month + seperator1 + strDate;
    return currentdate;
  }

  var url = '/client/lottery/history/' + getNowFormatDate()
  return axios.get(url).then((res) => {
    return Promise.resolve(res)
  })
}


